.catfood
{
    position: relative;

    display:         flex;
    display: -webkit-flex;
    display:     -ms-flex;
    display:         flex;
    flex-direction: column;

    width: 100%;
    min-height: 100%;
    padding: 20px 0;
    box-sizing: border-box;

    background-image: linear-gradient(to bottom, rgba(0,0,0,.5) 0%,transparent 50%,rgba(0,0,0,.5) 100%), url('../img/catfood-bg.jpg');

    justify-content: center;
    align-items: center;
    flex-grow: 1;

    &__container
    {

        box-sizing: border-box;
        width: 100%;

        @media (min-width: 380px)
        {
            padding-right: 15px;
            padding-left: 15px;
        }

        @media #{$desktop-widths}
        {
            max-width: 1150px;
        }
    }

    &__title
    {
        font: 100 36px/1 $font-exo;

        display: block;

        box-sizing: border-box;
        width: 100%;
        margin-bottom: 27px;
        padding: 0 15px;

        text-align: center;

        color: $font-white;
    }

    &__list
    {
        display: flex;
        flex-direction: column;

        width: 100%;

        justify-content: center;
        align-items: center;
    }
}
/* Internet Explorer 11+ */
_:-ms-fullscreen,
:root .catfood
{
    position: relative;

    display:         flex;
    display: -webkit-flex;
    display:     -ms-flex;
    display:         flex;
    flex-direction: column;

    width: 100%;
    height: 100%;
    padding: 20px 0;
    box-sizing: border-box;

    background-image: linear-gradient(to bottom, rgba(0,0,0,.5) 0%,transparent 50%,rgba(0,0,0,.5) 100%), url('../img/catfood-bg.jpg');

    justify-content: center;
    align-items: center;
    flex-grow: 1;
}

@media #{$tablet-widths}
{
    .catfood
    {
        &__list
        {
            flex-direction: row;

            flex-wrap: wrap;
            align-items: flex-start;
            justify-content: space-around;
        }
        &__title
        {
            padding: 0;
        }
    }
}

@media #{$desktop-widths}
{
    .catfood
    {
        padding: 0;

        &__list
        {
            justify-content: space-between;
        }
    }
}
